.element {
	position: relative;
	width: 100%;
	height: 100%;
	display: grid;
	place-items: center;

	box-shadow: inset 0 0 8px 5px rgba(0, 0, 0, 0.2);

	text-shadow: 0 0 4px rgb(255, 255, 255);
	color: rgb(255, 255, 255);
	font-family: sans-serif;

	-webkit-user-select: none;

	    -ms-user-select: none;

	        user-select: none;
	cursor: pointer;

	border: 1px solid var(--c-metal);
	border-radius: var(--b-radius);
}

.element-checkbox {
	display: none;
}

.element-checkbox:checked + .not-paired::after {
	opacity: 0.5;
}

.element-checkbox[readonly] + .element {
	pointer-events: none;
}

.hiding {
	background-color: rgba(200, 0, 50, 0.25);
}

@media screen and (min-width: 1280px) {
	.element {
		font-size: 3rem;
		box-shadow: inset 0 0 10px 10px rgba(0, 0, 0, 0.2);
		border: 3px solid var(--c-metal);
		border-radius: 0;
	}

	.border-left {
		border-left: 0;
	}

	.border-right {
		border-right: 0;
	}

	.border-top {
		border-top: 0;
	}

	.border-bottom {
		border-bottom: 0;
	}

	.border-top.border-right {
		border-top-right-radius: 16px;
	}

	.border-top.border-left {
		border-top-left-radius: 16px;
	}

	.border-bottom.border-right {
		border-bottom-right-radius: 16px;
	}

	.border-bottom.border-left {
		border-bottom-left-radius: 16px;
	}
}

.paired {
	color: transparent;
	text-shadow: none;
}

.not-paired {
	position: relative;
}

.not-paired::after {
	position: absolute;
	content: '';
	width: 75%;
	height: 75%;
	border-radius: 50%;
	opacity: 0;
}

.element-checkbox {
	display: none;
}

.element-checkbox:checked + .not-paired::after {
	opacity: 0.5;
}

.element-checkbox[readonly] + .element {
	pointer-events: none;
}

.border-left {
	border-left: 0;
	border-radius: 0 var(--b-radius) var(--b-radius) 0;
}

.border-right {
	border-right: 0;
	border-radius: var(--b-radius) 0 0 var(--b-radius);
}

.border-top {
	border-top: 0;
	border-radius: 0 0 var(--b-radius) var(--b-radius);
}

.border-bottom {
	border-bottom: 0;
	border-radius: var(--b-radius) var(--b-radius) 0 0;
}

.border-top.border-right {
	border-bottom-right-radius: 0;
	border-top-right-radius: var(--b-radius);
}

.border-top.border-left {
	border-bottom-left-radius: 0;
	border-top-left-radius: var(--b-radius);
}

.border-bottom.border-right {
	border-top-right-radius: 0;
	border-bottom-right-radius: var(--b-radius);
}

.border-bottom.border-left {
	border-top-left-radius: 0;
	border-bottom-left-radius: var(--b-radius);
}

